import React, { useState } from "react";
import logo from "../Recursos/logo-dark-512.png";
import { Navbar, Container, Nav, Form, Image } from "react-bootstrap";

import "./BarraLateralStyle.css";

function Encabezado() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  return (
    <>
      <Navbar
        bg="light"
        variant="light"
        expand="md"
        className="sticky-top flex-md-nowrap p-0 shadow"
      >
        <Navbar.Brand
          href="#"
          className="col-md-3 col-lg-2 me-0 px-3 d-flex align-items-center border-end "
        >
          <Image
            src={logo}
            alt="Company Logo"
            rounded
            className="me-2"
            style={{
              maxHeight: "55px",
              width: "auto",
            }}
          />
        </Navbar.Brand>

        <Navbar.Toggle
          aria-controls="sidebarMenu"
          className="position-absolute d-md-none"
        />

        <Nav className="ms-auto">
          <Nav.Link href="#" className="px-3">
            Cerrar sesin
          </Nav.Link>
        </Nav>
      </Navbar>
    </>
  );
}

export default Encabezado;
