import React, { useEffect, useState } from "react";
import Table from "react-bootstrap/Table";
import axios from "axios";
import { Trash, Pencil } from "react-bootstrap-icons";
import { Button, Modal } from "react-bootstrap";
import { Link } from "react-router-dom";
import {
  flexRender,
  getCoreRowModel,
  getFilteredRowModel,
  getPaginationRowModel,
  getSortedRowModel,
  useReactTable,
} from "@tanstack/react-table";
import Filters from "./Filters";

function ListadoClientes() {
  const [cliente, setCliente] = useState([]);
  const [columnFilters, setColumnFilters] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [selectedId, setSelectedId] = useState(null);

  const fetchClientes = () => {
    axios
      .get("http://localhost:8081/clientes")
      .then((res) => setCliente(res.data))
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    fetchClientes();
  }, []);

  const handleDelete = async () => {
    try {
      await axios.delete(
        "http://localhost:8081/eliminar-cliente/" + selectedId
      );
      console.log(selectedId);
      fetchClientes();
      setShowModal(false);
    } catch (err) {
      console.log(err);
    }
  };

  const columns = [
    {
      header: "Tipo",
      accessorKey: "tipo_persona",
      size: 50,
      cell: (props) => <p>{props.getValue()}</p>,
    },
    {
      header: "Cliente",
      accessorKey: "nombre_completo",
      cell: (props) => {
        const {
          nombre,
          apellido_paterno,
          apellido_materno,
          razon_comercial,
          razon_social,
        } = props.row.original;
        return (
          <p>
            {nombre && (
              <>
                {nombre} <br />
                {apellido_paterno} {apellido_materno}
              </>
            )}
            {razon_comercial && (
              <>
                {razon_comercial}
                <br />
                {razon_social}
              </>
            )}
          </p>
        );
      },
      filterFn: (row, columnId, filterValue) => {
        const {
          nombre,
          apellido_paterno,
          apellido_materno,
          razon_comercial,
          razon_social,
        } = row.original;
        const fullName =
          `${nombre} ${apellido_paterno} ${apellido_materno} ${razon_comercial} ${razon_social}`.toLowerCase();
        return fullName.includes(filterValue.toLowerCase());
      },
    },
    {
      header: "Contacto",
      accessorKey: "h",
      cell: (props) => {
        const {telefono, email1 } = props.row.original;
        return (
          <p>

            {telefono} {<br />}
            {email1}
          </p>
        );
      },
    },

    {
      header: "Accin",
      accessorKey: "id_cliente",
      cell: (props) => (
        <>
          <Button
            variant="link"
            className="p-0 border-0 text-danger"
            onClick={() => {
              setSelectedId(props.getValue());
              setShowModal(true);
            }}
          >
            <Trash size={20} />
          </Button>

          <Link
            variant="link"
            to={`/cliente/${props.getValue()}`}
            className="= p-0 ms-4 border-0 "
          >
            <Pencil />
          </Link>
        </>
      ),
    },
  ];

  const table = useReactTable({
    columns,
    data: cliente,
    initialState: {
      
      pagination: {
        pageIndex: 1, //custom initial page index
        pageSize: 7, //custom default page size
      },
    },
    state : {columnFilters},
    getCoreRowModel: getCoreRowModel(),
    getPaginationRowModel: getPaginationRowModel(),
    getSortedRowModel: getSortedRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
  });

  return (
    <>
      <Filters
        columnFilters={columnFilters}
        setColumnFilters={setColumnFilters}
      />

      <Table hover striped size="sm">
        <thead>
          {table.getHeaderGroups().map((headerGroup) => (
            <tr key={headerGroup.id}>
              {headerGroup.headers.map((header) => (
                <th key={header.id} style={{ width: `${header.getSize()}px` }}>
                  {header.column.columnDef.header}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody>
          {table.getRowModel().rows.map((row) => (
            <tr key={row.id}>
              {row.getVisibleCells().map((cell) => (
                <td key={cell.id} className="m-5">
                  {flexRender(cell.column.columnDef.cell, cell.getContext())}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </Table>
      <div>
        <Button
          onClick={() => table.firstPage()}
          disabled={!table.getCanPreviousPage()}
        >
          {"<<"}
        </Button>
        <Button
          onClick={() => table.previousPage()}
          disabled={!table.getCanPreviousPage()}
        >
          {"<"}
        </Button>
        <Button
          onClick={() => table.nextPage()}
          disabled={!table.getCanNextPage()}
        >
          {">"}
        </Button>
        <Button
          onClick={() => table.lastPage()}
          disabled={!table.getCanNextPage()}
        >
          {">>"}
        </Button>
      </div>
      

      <Modal centered show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>ALTO!</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <h4>Ests seguro de que deseas eliminar este cliente?</h4>
          <p>Esta accin no se puede deshacer.</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            Cancelar
          </Button>
          <Button variant="danger" onClick={handleDelete}>
            Eliminar
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default ListadoClientes;
