import React, { useState, useEffect, useCallback } from "react";
import {
  Accordion,
  Row,
  Col,
  Form,
  Button,
  Alert,
  Navbar,
  Modal,
  Spinner,
} from "react-bootstrap";
import useFetchClientes from "./FetchClientes";
import axios from "axios";
import Multiselect from "react-widgets/Multiselect";
import "react-widgets/styles.css";
import "./NuevoContenedorStyle.css";
import { Link, useNavigate, useParams } from "react-router-dom";
import {
  FormDatosGenerales,
  FormImagenes,
  FormDaos,
  FormMovimientos,
  FormDatosLlegada,
} from "./ContenedorForm";
import { useForm, SubmitHandler, Controller, } from "react-hook-form";

const NuevaLlegada = (props) => {
  const navigate = useNavigate();
  const id = props.id;
  const esUpdate = props.update;
  const esModoVista = props.view;
  const { listaClientes, fetchClientes } = useFetchClientes();
  const [cantidadDeContenedores, setCantidadDeContenedores] = useState(1);
  const [valoresDefault, setValoresDefault] = useState({
    fechaLlegada: "2025-01-01",
    horaLlegada: "10:00",
    transportista: "CCtrucking",
    operador: "Adan Guadalupe",
    numeroContenedor1: "FFAU3134201",
    eco: "T-100",
    estado1: "VACO",
    carga1: "IMO",
    num_candado1: "1234",
    peso1: 1000,
    tamano1: ["Otros"],
  });

  const [succesModalShow, setSuccesModalShow] = useState(false);
  const [failModalShow, setFailModalShow] = useState(false);
  const [contenedoresAgregados, setContenedoresAgregados] = useState([]);

  const {
    register,
    watch,
    handleSubmit,
    reset,
    formState,
    formState: { errors, isSubmitting },
    control,
  } = useForm({
    defaultValues:
      esUpdate || esModoVista
        ? async () => {
            try {
              const res = await axios.get(
                `http://localhost:8081/contenedor/${id}`
              );
              return {
                fechaLlegada: res.data.fechaLlegada.split("T")[0],
                horaLlegada: res.data.horaLlegada,
                transportista: res.data.transportista,
                operador: res.data.operador,
                numeroContenedor1: res.data.numeroContenedor,
                eco: res.data.eco,
                estado1: res.data.estado,
                carga1: res.data.carga,
                num_candado1: res.data.candado,
                peso1: res.data.peso,
                tamano1: res.data.tamano,
                cliente1: res.data.cliente,
              };
            } catch (err) {
              return valoresDefault;
            }
          }
        : valoresDefault,
    mode: "onChange",
    disabled: esModoVista || false,
  });

  useEffect(() => {
    if (formState.isSubmitSuccessful) {
      reset();
    }
  }, [formState, reset]);

  const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

  const onSubmit = async (data) => {
    setContenedoresAgregados([]);

    await delay(2000);

    let transformedData = Object.entries(data).reduce((acc, [key, value]) => {
      // eliminar espacios en blanco al inicio y al final
      if (typeof value === "string") {
        value = value.trim();
      }

      // Convertir a maysculas
      const keyMatch = key.match(/^([a-zA-Z_]+)(\d+)$/);

      //guardar el nmero de contenedor
      if (keyMatch && keyMatch[1] === "numeroContenedor") {
        setContenedoresAgregados((prev) => [...prev, value]);
      }

      if (keyMatch) {
        const [, fieldName, containerNum] = keyMatch;
        const containerIndex = parseInt(containerNum, 10);

        if (containerIndex <= cantidadDeContenedores) {
          // Special handling for cliente fields
          if (fieldName === "cliente" && value && value.CLIENTE) {
            acc[key] = value.CLIENTE.toUpperCase();
          } else if (Array.isArray(value)) {
            acc[key] = value.join(",");
          } else if (fieldName === "color" && value && value.color) {
            acc[key] = value.color.toUpperCase();
          } else {
            acc[key] = value;
          }
        }
      } else {
        acc[key] = value;
      }

      return acc;
    }, {});

    transformedData.CantidadContenedores = cantidadDeContenedores;
    transformedData.estatus = "En patio";
    transformedData.acceso = "Llegada";
    transformedData.user = "Max";

    axios
      .post("http://localhost:8081/agregar-contenedor", transformedData)
      .then((res) => {
        if (res.status === 200) {
          setSuccesModalShow(true);
        }
      })
      .catch((error) => {
        console.error("Error en la solicitud:", error);
        setFailModalShow(true);
      });
  };
  const [show, setShow] = useState(true);

  const [tamaosNoDisponibles, setTamaosNoDisponibles] = useState([]);
  const [tamaosSeleccionados, setTamaosSeleccionados] = useState("");

  const handleTamaosSeleccionados = (tamaos) => {
    setTamaosSeleccionados(tamaosSeleccionados + "," + tamaos);
  };

  const handleContenedoresNoDisponibles = (tamaos) => {
    if (cantidadDeContenedores == 2) {
    }
  };

  const handleContenedoresSelect = (e) => {
    if (e === "4") {
      setTamaosNoDisponibles("40");
    }
    setCantidadDeContenedores(e);
  };

  return (
    <>
      {succesModalShow && (
        <Modal_Succes
          show={succesModalShow}
          onHide={() => setSuccesModalShow(false)}
          numeroContendor={contenedoresAgregados}
        />
      )}
      {failModalShow && (
        <Modal_Fail
          show={failModalShow}
          onHide={() => setFailModalShow(false)}
        />
      )}
      <Form onSubmit={handleSubmit(onSubmit)} className="mt-0">
        <Accordion defaultActiveKey="0" flush alwaysOpen>
          <Accordion.Item eventKey="0">
            <Accordion.Header>
              <h5> Datos de llegada </h5>
            </Accordion.Header>
            <Accordion.Body>
              <FormDatosLlegada
                errors={errors}
                register={register}
                handleContenedoresSelect={handleContenedoresSelect}
              />

              {!esModoVista && (
                <Alert
                  variant="danger"
                  className=""
                  show={show}
                  onClose={() => setShow(false)}
                  dismissible
                >
                  <p>
                    Los campos marcados con <strong>*</strong> son obligatorios.
                    Por favor, asegrese de llenarlos correctamente.
                  </p>
                </Alert>
              )}
            </Accordion.Body>
          </Accordion.Item>

          {Array.from({ length: cantidadDeContenedores }, (_, j) => (
            <Accordion.Item key={`contenedor-${j + 1}`} eventKey={`${j + 1}`}>
              <Accordion.Header>
                {" "}
                <h5> Contenedor #{j + 1}</h5>
              </Accordion.Header>
              <Accordion.Body>
                <FormDatosGenerales
                  key={`datos-generales-${j + 1}`}
                  i={j + 1}
                  listaClientes={listaClientes}
                  actualizarClientes={fetchClientes}
                  register={register}
                  errors={errors}
                  control={control}
                  Controller={Controller}
                  cantidadDeContenedores={cantidadDeContenedores}
                  tamaosNoDisponibles={tamaosNoDisponibles}
                  setTamaosSeleccionados={setTamaosSeleccionados}
                  tamaosSeleccionados={tamaosSeleccionados}
                  handleTamaosSeleccionados={handleTamaosSeleccionados}
                />

                <hr />

                <FormImagenes
                  key={`imagenes-${j + 1}`}
                  i={j + 1}
                  errors={errors}
                  control={control}
                  Controller={Controller}
                  register={register}
                />
                <hr />

                <FormMovimientos
                  key={`movimientos-${j + 1}`}
                  i={j + 1}
                  listaClientes={listaClientes}
                  actualizarClientes={fetchClientes}
                  register={register}
                  errors={errors}
                  control={control}
                  Controller={Controller}
                  watch={watch}
                />
              </Accordion.Body>
            </Accordion.Item>
          ))}
        </Accordion>

        {!esModoVista && (
          <Navbar
            bg="light"
            variant="light"
            expand="md"
            className="sticky-bottom flex-md-nowrap p-0 shadow border-top"
            style={{
              position: "fixed",
              bottom: "0",
              width: "100%",
              left: "0",
              zIndex: "10",
              height: "60px",
            }}
          >
            <Col md={2}></Col>

            <Col md={10}>
              <div
                className="d-block justify-content-between align-items-center m-auto"
                style={{ width: "70%", maxWidth: "900px" }}
              >
                <div className="d-flex align-items-end justify-content-end">
                  <Button
                    variant="primary"
                    type="submit"
                    style={{
                      background: "#162759",
                      height: "40px",
                      borderColor: "#162759",
                    }}
                  >
                    {esUpdate
                      ? "Actualizar Contenedores"
                      : "Registrar Contenedores"}
                    {isSubmitting && (
                      <Spinner
                        as="span"
                        animation="border"
                        size="sm"
                        role="status"
                        aria-hidden="true"
                        className="ms-2"
                      />
                    )}
                  </Button>{" "}
                </div>
              </div>
            </Col>
          </Navbar>
        )}
      </Form>
    </>
  );
};

const Modal_Succes = (props) => {
  return (
    <Modal {...props} centered aria-labelledby="contained-modal-title-vcenter">
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {props.numeroContendor.length === 1
            ? "Contenedor registrado"
            : "Contenedores registrados"}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {props.numeroContendor.map((contenedor, index) =>
          contenedor ? (
            <p key={index}>
              Contenedor #{index + 1}: {contenedor}{" "}
            </p>
          ) : null
        )}
      </Modal.Body>
      <Modal.Footer>
        <Button href="/listado-contenedores" variant="primary">
          Mostrar en inventario
        </Button>
        <Button variant="secondary" onClick={props.onHide}>
          Seguir agregando contenedores
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

const Modal_Fail = (props) => {
  return (
    <Modal {...props} centered aria-labelledby="contained-modal-title-vcenter">
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Error al registrar contenedor
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <p>El contenedor no se podido agregar, intente nuevamente</p>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.onHide}>
          Cerrar
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default NuevaLlegada;
