import React, { useState } from "react";
import { Row, Col, Form, Button } from "react-bootstrap";

const Unidades = () => {
  const NuevaUnidad = () => {
    const [Unidad, setUnidad] = useState({
      nmeroUnidad: "",
      marca: "",
      operador: "",
      placas: "",
      licencia: "",
      transportista: "",
    });

    const handleChange = (e) => {
      setUnidad({
        ...Unidad,
        [e.target.name]: e.target.value,
      });
    };

    const handleSubmit = (e) => {
      e.preventDefault();
      console.log("Datos de la unidad:", Unidad);
    };

    return (
      <>
        <Form onSubmit={handleSubmit}>
          <Row className="gy-3 mb-3">
            
              <Col md={4}>
                <Form.Group controlId="nmeroUnidad">
                  <Form.Label>Nmero de Unidad</Form.Label>
                  <Form.Control
                    type="text"
                    name="nmeroUnidad"
                    placeholder="Nmero de Unidad"
                    value={Unidad.nmeroUnidad}
                    onChange={handleChange}
                    required
                  />
                </Form.Group>
              </Col>
              <Col md={4}>
                <Form.Group controlId="marca">
                  <Form.Label>Marca</Form.Label>
                  <Form.Select
                    name="marca"
                    value={Unidad.marca}
                    onChange={handleChange}
                    required
                  >
                    <option value="">Selecciona una marca</option>
                    <option value="Freightliner">Freightliner</option>
                    <option value="Kenworth">Kenworth</option>
                    <option value="Peterbilt">Peterbilt</option>
                    <option value="International">International</option>
                    <option value="Volvo">Volvo</option>
                    <option value="Mack">Mack</option>
                    <option value="Scania">Scania</option>
                    <option value="Mercedes-Benz">Mercedes-Benz</option>
                    <option value="MAN">MAN</option>
                    <option value="DAF">DAF</option>
                    <option value="Iveco">Iveco</option>
                    <option value="Western Star">Western Star</option>
                    <option value="Hino">Hino</option>
                    <option value="Isuzu">Isuzu</option>
                    <option value="Fuso">Fuso</option>
                  </Form.Select>
                </Form.Group>
              </Col>
            

            <Col md={4}>
              <Form.Group controlId="placas">
                <Form.Label>Placas</Form.Label>
                <Form.Control
                  type="text"
                  name="placas"
                  placeholder="Placas"
                  value={Unidad.placas}
                  onChange={handleChange}
                  required
                />
              </Form.Group>
            </Col>
            <Col md={4}>
              <Form.Group controlId="licencia">
                <Form.Label>Licencia</Form.Label>
                <Form.Control
                  type="text"
                  name="licencia"
                  placeholder="Licencia"
                  value={Unidad.licencia}
                  onChange={handleChange}
                  required
                />
              </Form.Group>
            </Col>
            <Col md={4}>
              <Form.Group controlId="transportista">
                <Form.Label>Transportista</Form.Label>
                <Form.Control
                  type="text"
                  name="transportista"
                  placeholder="Transportista"
                  value={Unidad.transportista}
                  onChange={handleChange}
                  required
                />
              </Form.Group>
            </Col>
            <Col md={12}>
              <Form.Group controlId="operador">
                <Form.Label>Operador</Form.Label>
                <Form.Control
                  type="text"
                  name="operador"
                  placeholder="Operador"
                  value={Unidad.operador}
                  onChange={handleChange}
                  required
                />
              </Form.Group>
            </Col>
          </Row>
          <Button type="submit">Guardar Unidad</Button>
        </Form>
      </>
    );
  };

  return <NuevaUnidad />;
};

export default Unidades;
