import React from "react";
import { useEffect, useState } from "react";
import {
  Accordion,
  Row,
  Col,
  Form,
  Button,
  Alert,
  Navbar,
  Modal,
  Spinner,
} from "react-bootstrap";
import "../Contenedores/formulario/NuevoContenedorStyle.css";
import { useForm, SubmitHandler, Controller } from "react-hook-form";
import axios from "axios";
import { DropdownList } from "react-widgets";
import { set } from "date-fns";

function FormularioMovimientos() {
  const {
    register,
    watch,
    handleSubmit,
    reset,
    setValue,
    formState,
    formState: { errors, isSubmitting },
    control,
  } = useForm();

  const [contenedores, setContenedores] = useState([]);
  const contenedorSeleccionado = watch("numeroContenedor", false);

  const movmientos = [
    "Camin a piso",
    "Camin a camin",
    "Estadas",
    "Inspeccin ocular",
    "Movimiento para lavado",
    "Movimiento para fumigacin",
    "Movimiento para cambio de sello",
  ];

  useEffect(() => {
    axios.get("http://localhost:8081/contenedores").then((res) => {
      setContenedores(res.data);
      console.log(res.data);
    });
  }, []);

  return (
    <>
      <Accordion defaultActiveKey="0" alwaysOpen flush>
        <Accordion.Item eventKey="0" aria-expanded="true">
          <Accordion.Header>
            <h5 className="text-center">Formulario de movimientos</h5>
          </Accordion.Header>
          <Accordion.Body>
            <Form onSubmit={handleSubmit((data) => console.log(data))}>
              <Row className="gy-3 mb-3">
                <Col md={6}>
                  <Form.Group>
                    <Form.Label className="d-flex align-items-center">
                      Contenedor <span className="text-danger"> *</span>
                    </Form.Label>
                    <Controller
                    
                      name={"numeroContenedor"}
                      control={control}
                      rules={{ required: true }}
                      render={({ field }) => (
                        <DropdownList
                       
                          textField="numeroContenedor"
                          defaultValue={"Seleccione un contenedor"}
                          data={contenedores}
                          {...field}
                          onChange={(value) => {
                            setValue(
                              "Ubicacin",
                              contenedores.find(
                                (c) =>
                                  c.numeroContenedor === value.numeroContenedor
                              )?.color || "No se tiene registro"
                            );
                          }}
                        />
                      )}
                    />
                    {errors.numeroContenedor && (
                      <p className="text-danger">
                        Se requiere seleccionar un contenedor
                      </p>
                    )}
                  </Form.Group>
                </Col>

                <Col md={6}>
                  <Form.Group>
                    <Form.Label>
                      Seleccione un movimiento{" "}
                      <span className="text-danger">*</span>
                    </Form.Label>
                    <Form.Select
                      {...register("tipo_movimiento", { required: true })}
                    >
                      {movmientos.map((movimiento, index) => (
                        <option
                          key={`${movimiento}-${index}`}
                          value={movimiento}
                        >
                          {movimiento}
                        </option>
                      ))}
                    </Form.Select>
                  </Form.Group>
                </Col>

                <Col md={6}>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>
                      Ubicacin <span className="text-danger">*</span>
                    </Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Ubicacin"
                      {...register("Ubicacin", {
                        required: "Campo requerido",
                      })}
                    />
                    {errors.cliente && (
                      <span className="text-danger">
                        {errors.cliente.message}
                      </span>
                    )}
                  </Form.Group>
                </Col>

                <Col md={6}>
                  <Form.Group>
                    <Form.Label>
                      Destino <span className="text-danger">*</span>
                    </Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Origen"
                      {...register("lugar_origen", {
                        required: "Campo requerido",
                      })}
                    />
                    {errors.cliente && (
                      <span className="text-danger">
                        {errors.cliente.message}
                      </span>
                    )}
                  </Form.Group>
                </Col>

                <Col md={6}>
                  <Form.Group>
                    <Form.Label>
                      Seleccione un gruyero{" "}
                      <span className="text-danger">*</span>
                    </Form.Label>
                    <Form.Select {...register("gruyero", { required: true })}>
                      <option>Gruyero 1</option>
                      <option>Gruyero 2</option>
                    </Form.Select>
                    {errors.gruyero && (
                      <p className="text-danger">
                        Se requiere seleccionar un gruyero
                      </p>
                    )}
                  </Form.Group>
                  <Form.Select dropdown="true">
                    <option>Gruyero 1</option>
                    <option>Gruyero 2</option>
                  </Form.Select>
                </Col>
              </Row>

              {contenedorSeleccionado}

              <Button variant="primary" type="submit" disabled={isSubmitting}>
                Guardar
              </Button>
            </Form>
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>
    </>
  );
}

export default FormularioMovimientos;
