import React, { useEffect, useState } from "react";
import { Table, Navbar, Col } from "react-bootstrap";
import axios from "axios";
import { Trash, Pencil, Filter, Eye } from "react-bootstrap-icons";
import { Button, Modal, Form } from "react-bootstrap";
import { Link } from "react-router-dom";
import {
  flexRender,
  getCoreRowModel,
  getFilteredRowModel,
  getPaginationRowModel,
  getSortedRowModel,
  useReactTable,
  globalFilterFn,
  getFacetedMinMaxValues,
  getFacetedRowModel,
  getFacetedUniqueValues,
  onGlobalFilterChange,
  fuzzyFilter,
} from "@tanstack/react-table";
import Filters from "../../Complementos/Filters";
import { format, parseISO, isAfter, isBefore } from "date-fns";
import NuevaLlegada from "../formulario/NuevaLlegada";
import "./listaStyles.css";

function ListadoContenedores() {
  const [contenedores, setContenedores] = useState([]);
  const [columnFilters, setColumnFilters] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [selectedId, setSelectedId] = useState(null);
  const [showContenedor, setShowContenedor] = useState(false);
  const [editContenedor, setEditContenedor] = useState(false);

  const [globalFilter, setGlobalFilter] = useState("");

  const fetchContenedores = () => {
    axios
      .get("http://localhost:8081/contenedores")
      .then((res) => setContenedores(res.data))
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    fetchContenedores();
  }, []);

  const handleDelete = async () => {
    try {
      await axios.delete(
        "http://localhost:8081/eliminar-contenedor/" + selectedId
      );
      fetchContenedores();
      setShowModal(false);
    } catch (err) {
      console.log(err);
    }
  };

  const columns = [
    {
      header: "ID",
      accessorKey: "id_contenedor",

      meta: { filterVariant: "number" },
      cell: (props) => <p className="m-0">{props.getValue()}</p>,
    },
    {
      header: "Contenedor",
      accessorKey: "numeroContenedor",
      cell: (props) => <p className="m-0">{props.getValue()}</p>,
    },
    {
      header: "Cliente",
      accessorKey: "cliente",
      cell: (props) => <p className="m-0 text-nowrap">{props.getValue()}</p>,
    },
    {
      header: "Llegada",
      accessorFn: (row) => row.fechaLlegada, // Mantenemos fechaLlegada como valor accesible para filtros
      id: "llegada", // Identificador nico para la columna
      meta: { filterVariant: "fecha" },
      cell: (props) => {
        const row = props.row.original;
        return (
          <div>
            <p className="m-0">
              {format(new Date(row.fechaLlegada), "dd-MM-yy")}
            </p>
            <p className="m-0 text-muted">{row.horaLlegada}</p>
          </div>
        );
      },
      filterFn: (row, columnId, filterValue) => {
        if (!filterValue || filterValue.length < 2) return true;

        const fecha = parseISO(row.original.fechaLlegada);
        const desde = filterValue[0] ? parseISO(filterValue[0]) : null;
        const hasta = filterValue[1] ? parseISO(filterValue[1]) : null;

        return (
          (!desde || isAfter(fecha, desde)) &&
          (!hasta || isBefore(fecha, hasta))
        );
      },
    },
    {
      header: "Carga",
      accessorFn: (row) => row.estado, // Mantenemos estado + carga como valor accesible para filtros
      meta: { filterVariant: "select" },
      cell: (props) => {
        const row = props.row.original;
        return (
          <p className="m-0">
            {row.estado} {row.carga}
          </p>
        );
      },
    },
    {
      header: "Tamao",
      accessorKey: "tamano",
      cell: (props) => <p className="m-0">{props.getValue()}</p>,
    },
    {
      header: "Estatus",
      accessorKey: "estatus",
      cell: (props) => <p className="m-0">{props.getValue()}</p>,
    },
    {
      header: "Accin",
      accessorFn: (row) => row.id_contenedor,
      cell: (props) => (
        <>
          <Button
            variant="link"
            className="p-0 border-0 text-danger"
            onClick={() => {
              setSelectedId(props.getValue());
              console.log(selectedId);
              setShowModal(true);
            }}
          >
            <Trash size={20} />
          </Button>

          <Link
            variant="link"
            onClick={() => {
              setSelectedId(props.getValue());
              setEditContenedor(true);
            }}
            className="p-0 ms-4 border-0"
          >
            <Pencil />
          </Link>

          <Button
            variant="link"
            onClick={() => {
              setSelectedId(props.getValue());
              setShowContenedor(true);
            }}
            className="p-0 ms-4 border-0 text-success"
          >
            <Eye />
          </Button>
        </>
      ),
    },
  ];

  const table = useReactTable({
    columns,
    data: contenedores,
    state: { columnFilters, globalFilter },

    initialState: {
      pagination: {
        pageIndex: 0, //custom initial page index
        pageSize: 15, //custom default page size
      },
      sorting: [
        {
          id: "id_contenedor",
          desc: true,
        },
      ],
    },
    onColumnFiltersChange: setColumnFilters,
    getCoreRowModel: getCoreRowModel(),
    getFilteredRowModel: getFilteredRowModel(), //client-side filtering
    getSortedRowModel: getSortedRowModel(),
    getPaginationRowModel: getPaginationRowModel(),
    getFacetedRowModel: getFacetedRowModel(), // client-side faceting
    getFacetedUniqueValues: getFacetedUniqueValues(), // generate unique values for select filter/autocomplete
    getFacetedMinMaxValues: getFacetedMinMaxValues(), // generate min/max values for range filter
    onGlobalFilterChange: setGlobalFilter,
    globalFilterFn: "includesString",
    debugTable: true,
    debugHeaders: true,
    debugColumns: false,
  });

  return (
    <>
      <div className="col-4 pb-3">
        <Form.Control
          type="text"
          size="sm"
          value={globalFilter ?? ""}
          onChange={(e) => table.setGlobalFilter(String(e.target.value))}
          className="p-2 font-lg shadow border border-block fit-content"
          placeholder="Bscar en tabla..."
        />
      </div>

      <Table hover striped size="sm" style={{ overflowY: "scroll" }}>
        <thead style={{ fontSize: "0.9rem", textWrapMode: "nowrap" }}>
          {table.getHeaderGroups().map((headerGroup) => (
            <tr key={headerGroup.id}>
              {headerGroup.headers.map((header) => (
                <th key={header.id} style={{ width: `${header.getSize()}px` }}>
                  {header.column.getCanFilter() ? (
                    <div>
                      <Filters column={header.column} />
                    </div>
                  ) : (
                    header.column.columnDef.header
                  )}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody style={{ fontSize: "0.8rem" }}>
          {table.getRowModel().rows.map((row) => (
            <tr key={row.id} className="m-0 p-0">
              {row.getVisibleCells().map((cell) => (
                <td key={cell.id} className="m-0">
                  {flexRender(cell.column.columnDef.cell, cell.getContext())}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </Table>

      <Navbar
        bg="light"
        variant="light"
        expand="md"
        className="sticky-bottom flex-md-nowrap p-0 shadow border-top"
        style={{
          position: "fixed",
          bottom: "0",
          width: "100%",
          left: "0",
          zIndex: "10",
          height: "60px",
        }}
      >
        <Col md={2}></Col>

        <Col md={10}>
          <div
            className="d-flex flex-column justify-content-center m-auto"
            style={{ width: "70%", maxWidth: "900px" }}
          >
            <div className="d-flex justify-content-center">
              <Button
                onClick={() => table.firstPage()}
                disabled={!table.getCanPreviousPage()}
                variant="text"
                className="border-0"
              >
                {"<<"}
              </Button>
              <Button
                variant="text"
                onClick={() => table.previousPage()}
                disabled={!table.getCanPreviousPage()}
                className="border-0"
              >
                <strong>{"<"}</strong>
              </Button>
              <Button
                onClick={() => table.nextPage()}
                disabled={!table.getCanNextPage()}
                variant="text"
                className="border-0"
              >
                {">"}
              </Button>
              <Button
                onClick={() => table.lastPage()}
                disabled={!table.getCanNextPage()}
                variant="text"
                className="border-0"
              >
                {">>"}
              </Button>
            </div>

            <div className="d-flex justify-content-center">
              <span>
                Pgina{" "}
                <strong className="me-3">
                  {table.getState().pagination.pageIndex + 1} de{" "}
                  {table.getPageCount()}{" "}
                </strong>
                {table.getRowModel().rows.length.toLocaleString()} de{" "}
                {table.getRowCount().toLocaleString()} contenedores
              </span>
            </div>
          </div>
        </Col>
      </Navbar>

      <Modal centered show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>ALTO!</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <h4>Ests seguro de que deseas eliminar este contenedor?</h4>
          <p>Esta accin no se puede deshacer.</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            Cancelar
          </Button>
          <Button variant="danger" onClick={handleDelete}>
            Eliminar
          </Button>
        </Modal.Footer>
      </Modal>

      <Modal
        centered
        show={showContenedor}
        onHide={() => setShowContenedor(false)}
        size="xl"
      >
        <Modal.Header closeButton>
          <Modal.Title>Contenedor</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <NuevaLlegada view={true} id={selectedId} />
        </Modal.Body>
      </Modal>

      <Modal
        centered
        show={editContenedor}
        onHide={() => setEditContenedor(false)}
        className="cufsdf"
        size="xl"
      >
        <Modal.Header closeButton>
          <Modal.Title>Contenedor</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <NuevaLlegada view={false} update={true} id={selectedId} />
        </Modal.Body>
      </Modal>
    </>
  );
}

export default ListadoContenedores;
