import React, { useState, useEffect } from "react";
import { Container, Row, Col, Nav, Tab, Form, Button } from "react-bootstrap";
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";

function NuevoCliente({ setShowModal, AgregadoDesdeContenedor, update, actualizarClientes }) {
  const navigate = useNavigate();
  const { id_cliente } = useParams();
  const esUpdate = update;
  const esAgregadoDesdeContenedor = AgregadoDesdeContenedor;
  const [isPersonaFisica, setIsPersonaFisica] = useState(true);

  const [cliente, setCliente] = useState({
    tipo_persona: "PF",
    nombre: "max",
    apellido_paterno: "Prez",
    apellido_materno: "Garca",
    razon_social: "",
    razon_comercial: "",
    representante_legal: "Carlos Lpez",
    rfc_nit: "JUAPG123456789",
    telefono: "1234567890",
    transportista: "Transportes XYZ",
    credito_disponible: "10000",
    email1: "juan.perez@example.com",
    email2: "contacto@example.com",
    email3: "ventas@example.com",
    email4: "soporte@example.com",
    estado: "Estado de Mxico",
    municipio: "Naucalpan",
    ciudad: "Ciudad de Mxico",
    colonia: "Centro",
    calle: "Av. Reforma",
    numero: "123",
    codigo_postal: "54321",
    referencia: "Frente al parque",
  });

  useEffect(() => {
    if (esUpdate && id_cliente) {
      axios
        .get(`http://localhost:8081/cliente/${id_cliente}`)
        .then((res) => {
          setCliente(res.data);
          console.log(res.data);
          setIsPersonaFisica(res.data.tipo_persona === "PF");
        })
        .catch((err) => console.log(err));
    }
  }, [esUpdate, id_cliente]);

  const handleChange = (e) => {
    setCliente({
      ...cliente,
      [e.target.name]: e.target.value,
    });
  };

  const handlePersonaChange = (type) => {
    setIsPersonaFisica(type === "PF");
    setCliente((prevCliente) => ({
      ...prevCliente,
      tipo_persona: type,
      nombre: type === "PF" ? prevCliente.nombre : "",
      apellido_paterno: type === "PF" ? prevCliente.apellido_paterno : "",
      apellido_materno: type === "PF" ? prevCliente.apellido_materno : "",
      razon_social: type === "PM" ? prevCliente.razon_social : "",
      razon_comercial: type === "PM" ? prevCliente.razon_comercial : "",
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const clienteFormatted = {
      ...cliente,
    };

    if (esUpdate) {
      axios
        .put(`http://localhost:8081/cliente/${id_cliente}`, clienteFormatted)
        .then((res) => {
          console.log("Cliente actualizado:", res);
          navigate("/listado-clientes");
        })
        .catch((err) => console.error("Error al actualizar cliente:", err));
    } else {
      axios
        .post("http://localhost:8081/agregar-cliente", clienteFormatted)
        .then((res) => {
          console.log("Cliente creado:", res);
          if (esAgregadoDesdeContenedor) {
            setShowModal(false);
            actualizarClientes(); // Call the callback to update the clients' data
          }
        })
        .catch((err) => console.error("Error al agregar cliente:", err));
    }
  };

  return (
    <>
      <Tab.Container id="tabs" defaultActiveKey="DatosCliente">
        <Nav variant="tabs">
          <Nav.Item>
            <Nav.Link eventKey="DatosCliente">Datos del cliente</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="Direccion">Direccin</Nav.Link>
          </Nav.Item>
        </Nav>

        <Form onSubmit={handleSubmit}>
          <Tab.Content>
            <Tab.Pane eventKey="DatosCliente">
              <h5>
                <strong>Datos del cliente</strong>
              </h5>
              <hr />

              <Form.Group className="mb-3">
                <Form.Check
                  type="radio"
                  label="Persona fsica"
                  name="tipo_persona"
                  id="personaFsica"
                  value="PF"
                  checked={isPersonaFisica}
                  onChange={() => handlePersonaChange("PF")}
                  disabled={esUpdate}
                />
                <Form.Check
                  type="radio"
                  label="Persona Moral"
                  name="tipo_persona"
                  id="personaMoral"
                  value="PM"
                  checked={!isPersonaFisica}
                  onChange={() => handlePersonaChange("PM")}
                  disabled={esUpdate}
                />
              </Form.Group>

              {isPersonaFisica ? (
                <Row className="gy-3 mb-3">
                  <Col md={12}>
                    <Form.Label>Nombre(s)</Form.Label>
                    <Form.Control
                      type="text"
                      name="nombre"
                      value={cliente.nombre}
                      onChange={handleChange}
                      required
                    />
                  </Col>
                  <Col md={6}>
                    <Form.Label>Apellido paterno</Form.Label>
                    <Form.Control
                      type="text"
                      name="apellido_paterno"
                      value={cliente.apellido_paterno}
                      onChange={handleChange}
                      required
                    />
                  </Col>
                  <Col md={6}>
                    <Form.Label>Apellido materno</Form.Label>
                    <Form.Control
                      type="text"
                      name="apellido_materno"
                      value={cliente.apellido_materno}
                      onChange={handleChange}
                      required
                    />
                  </Col>
                </Row>
              ) : (
                <Row className="gy-3 mb-3">
                  <Col md={6}>
                    <Form.Label>Razn social</Form.Label>
                    <Form.Control
                      type="text"
                      name="razon_social"
                      value={cliente.razon_social}
                      onChange={handleChange}
                      required
                    />
                  </Col>
                  <Col md={6}>
                    <Form.Label>Razn comercial</Form.Label>
                    <Form.Control
                      type="text"
                      name="razon_comercial"
                      value={cliente.razon_comercial}
                      onChange={handleChange}
                      required
                    />
                  </Col>
                </Row>
              )}

              <hr />

              <Row className="gy-3">
                <Col md={6}>
                  <Form.Label>Representante legal</Form.Label>
                  <Form.Control
                    type="text"
                    name="representante_legal"
                    value={cliente.representante_legal}
                    onChange={handleChange}
                    required
                  />
                </Col>
                <Col md={6}>
                  <Form.Label>RFC / NIT</Form.Label>
                  <Form.Control
                    type="text"
                    name="rfc_nit"
                    value={cliente.rfc_nit}
                    onChange={handleChange}
                    required
                    pattern="[A-Z0-9]{10,17}"
                  />
                </Col>
                <Col md={4}>
                  <Form.Label>Telfono</Form.Label>
                  <Form.Control
                    type="tel"
                    name="telefono"
                    value={cliente.telefono}
                    onChange={handleChange}
                    required
                    pattern="\d{10}"
                  />
                </Col>
                <Col md={4}>
                  <Form.Label>Transportista</Form.Label>
                  <Form.Control
                    type="text"
                    name="transportista"
                    value={cliente.transportista}
                    onChange={handleChange}
                    required
                  />
                </Col>
                <Col md={4}>
                  <Form.Label>Crdito total disponible</Form.Label>
                  <Form.Control
                    type="number"
                    name="credito_disponible"
                    value={cliente.credito_disponible}
                    onChange={handleChange}
                    required
                  />
                </Col>

                <Col md={6}>
                  <Form.Label>Contacto Email 1</Form.Label>
                  <Form.Control
                    type="text"
                    name="email1"
                    value={cliente.email1}
                    onChange={handleChange}
                    required
                  />
                </Col>
                <Col md={6}>
                  <Form.Label>Contacto Email 2</Form.Label>
                  <Form.Control
                    type="email"
                    name="email2"
                    value={cliente.email2}
                    onChange={handleChange}
                  />
                </Col>
                <Col md={6}>
                  <Form.Label>Contacto Email 3</Form.Label>
                  <Form.Control
                    type="email"
                    name="email3"
                    value={cliente.email3}
                    onChange={handleChange}
                  />
                </Col>
                <Col md={6}>
                  <Form.Label>Contacto Email 4</Form.Label>
                  <Form.Control
                    type="email"
                    name="email4"
                    value={cliente.email4}
                    onChange={handleChange}
                  />
                </Col>
              </Row>
            </Tab.Pane>

            <Tab.Pane eventKey="Direccion">
              <Row className="gy-3">
                <Col md={4}>
                  <Form.Label>Estado</Form.Label>
                  <Form.Control
                    type="text"
                    name="estado"
                    value={cliente.estado}
                    onChange={handleChange}
                    required
                  />
                </Col>
                <Col md={4}>
                  <Form.Label>Municipio</Form.Label>
                  <Form.Control
                    type="text"
                    name="municipio"
                    value={cliente.municipio}
                    onChange={handleChange}
                    required
                  />
                </Col>
                <Col md={4}>
                  <Form.Label>Ciudad</Form.Label>
                  <Form.Control
                    type="text"
                    name="ciudad"
                    value={cliente.ciudad}
                    onChange={handleChange}
                    required
                  />
                </Col>
                <Col md={4}>
                  <Form.Label>Colonia</Form.Label>
                  <Form.Control
                    type="text"
                    name="colonia"
                    value={cliente.colonia}
                    onChange={handleChange}
                    required
                  />
                </Col>
                <Col md={4}>
                  <Form.Label>Calle</Form.Label>
                  <Form.Control
                    type="text"
                    name="calle"
                    value={cliente.calle}
                    onChange={handleChange}
                    required
                  />
                </Col>
                <Col md={2}>
                  <Form.Label>Nmero</Form.Label>
                  <Form.Control
                    type="text"
                    name="numero"
                    value={cliente.numero}
                    onChange={handleChange}
                    required
                    pattern="\d+"
                  />
                </Col>
                <Col md={2}>
                  <Form.Label>C.P.</Form.Label>
                  <Form.Control
                    type="text"
                    name="codigo_postal"
                    value={cliente.codigo_postal}
                    onChange={handleChange}
                    required
                    pattern="\d+"
                  />
                </Col>
                <Col md={12}>
                  <Form.Label>Referencia</Form.Label>
                  <Form.Control
                    type="text"
                    name="referencia"
                    value={cliente.referencia}
                    onChange={handleChange}
                  />
                </Col>
              </Row>
            </Tab.Pane>
          </Tab.Content>

          <hr />
          <div className="text-center">
            <button type="submit" className="btn btn-primary">
              {esUpdate ? "Actualizar Cliente" : "Agregar Cliente"}
            </button>
          </div>
        </Form>
      </Tab.Container>
    </>
  );
}

export default NuevoCliente;
